<template>
  <div class="table1">
    <div class="add cb">
      <div class="add-left fl">人员信息</div>
    </div>
    <el-table ref="multipleTable" :data="personList" tooltip-effect="dark" type="index" style="width: 100%">
       <el-table-column prop="staff" label="姓名" width="110">
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row|showStaffName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="baseAddress" label="籍贯" width="130">
        <template scope="scope">
          <div>{{scope.row.baseAddress}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="school" label="毕业学校" show-overflow-tooltip>
        <template scope="scope">
          <div >{{scope.row.school}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="major" label="专业"  show-overflow-tooltip>
        <template scope="scope">
          <div>{{scope.row.major}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="groupTypevalue" label="归属小组"  width="140">
        <template scope="scope">
          <div >{{scope.row.groupTypevalue}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="staffTeacherName" label="导师名称"  show-overflow-tooltip>
        <template scope="scope">
          <div >{{scope.row.staffTeacherName}}</div>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="status" label="状态"  show-overflow-tooltip>
        <template scope="scope">
          <div >{{scope.row.status}}</div>
        </template>
      </el-table-column> -->
      <el-table-column prop="beginDate" label="入司时间" width="170">
        <template scope="scope">
          <div >{{scope.row.beginDate}}</div>
        </template>
      </el-table-column>
		</el-table>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  name: "table1",
  data() {
    return {
      dialogVisible: false,
      dialogFormVisible: false,
      addDetail: {},
      editDetail: {},
      index: 0,
      personList: [],

      subjectName: "",
      staffname: "",
      staffID: "",
      editid: ""
    };
  },
  mounted: function() {
    this.getAauthor();
  },
  filters: {
    showStaffName(row) {
      if (row.staff) {
        return row.staff.staffname;
      } else {
        return "";
      }
    }
  },
  methods: {
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[4];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
          this.getList();
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        staffId: this.staffID
      };
      console.log(params);
      service
        .getMyselfMessage(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.list = res.data;

          this.personList = [this.list];
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #eee;
}
table thead th {
  background: #e5eff8;
  padding: 22px 18px 22px 18px;
  text-align: center;
}
table tbody td {
  padding: 10px 0px 10px 18px;
  text-align: left;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}
table span {
  margin: 0 10px;
  cursor: pointer;
}
.delete {
  color: red;
}
.edit {
  color: #008cd5;
}
.add {
  border: 1px solid #eee;
  margin-bottom: 10px;
  height: 65px;
  padding: 15px 0px 15px 15px;
}
tbody tr:hover {
  background-color: #e5f0f86c;
}
.add button {
  background: #008cd5;
  border: 0;
  cursor: pointer;
  padding: 6px 15px;
  border-radius: 3px;
  color: #fff;
  margin-left: 10px;
}
.hello {
  margin-top: -10px;
  margin-bottom: 10px;
}
.header-search-input {
  display: inline-block;
  right: 1%;
  margin-left: 12px;
  width: 300px;
}
.header-search-input input {
  position: relative;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #d8dce5;
  display: inline-block;
  height: 35px;
  line-height: 1;
  outline: 0;
  padding: 0 25px 0 15px;
  width: 280px;
  margin-right: -4px;
}
.query-search-btn {
  color: #c2d6e7;
  position: relative;
  display: inline-block;
  margin-left: -30px;
  cursor: pointer;
}
.add-left {
  font-size: 16px;
}
.hello {
  position: fixed;
  bottom: 0px;
  right: 0px;
}
.add-right {
  margin-right: 80px;
}
</style>



